.franchise-service {

  color: #fff;

  &__wrapper {
    @media screen and (min-width: $w1440) {
      display: flex;
    }
  }


  &__h2 {
    @include font-h2;
    padding-bottom: 0.35em;
    color: #fff;

  }

  &__icon {
    display: block;
    width: 100%;
    max-width: 128px;
    padding-top: 95%;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    display: none;
    margin-bottom: 25px;


    @media screen and (min-width: $w1200) {
      display: block;
    }
  }

  &__text {
    & p {
      margin: 1.3em 0;
    }

    padding-bottom: 30px;
  }


  &__text-icon {
    width: 130px;
  }

  &__text-block {

    line-height: 150%;

    @media screen and (min-width: $w1440) {
      width: calc(100% - 130px);
      padding-left: 40px;
    }


    & strong {
      font-weight: 400;
      font-size: 18px;

      @media screen and (min-width: $w1440) {
        font-size: 20px;
      }
    }
  }

  &__subtitle {
    @include font-h4;
    padding-bottom: 25px;
  }

  &__adv {

    padding-bottom: 30px;

    & ul {

      display: flex;
      flex-wrap: wrap;
      margin: -4px;

      & li {
        border-radius: 8px;
        background: var(--blue-medium, #515E93);
        padding: 24px;
        margin: 4px;
        flex-grow: 1;
        display: flex;
        align-items: flex-start;

        @media screen and (max-width: $w1440) {
          flex-basis: 320px;
        }

        &::before {
          content: '';
          min-width: 24px;
          width: 24px;
          height: 24px;
          display: inline-block;
          @include bgimage-contain;
          margin-right: 12px;
          background-image: url(data:image/svg+xml;base64,);
        }

      }
    }
  }

  &__note {
    max-width: 716px;
    line-height: 140%;
  }



}

.block-fullpage.colored.marginared {
  @media screen and (min-width: $w1440) {
    margin-bottom: 80px;
    padding-top: 65px;
  }

  @media screen and (min-width: $w992) {
    @media screen and (min-width: $w1440) {
      margin-bottom: 30px;
    }
  }
}