.logo-banner {

  margin-top: 30px;

  @media screen and (min-width: 1820px) {
    margin-right: -80px;
  }

  @media screen and (min-width: $w1200) {
    margin-right: -30px;
  }



  &__wrapper-md {
    background-color: #ECF0F5;
    margin: 0 -16px;

    @media screen and (min-width: 576px) {
      background-color: initial;
      margin: 0;

    }

    @media screen and (min-width: 1200px) {
      display: none;
    }
  }

  ul {
    padding: 16px;

    li {
      display: flex;
      align-items: center;
      margin: 8px 0;

      h5 {
        font-weight: 700;
        font-size: 20px;
        color: #253678;
      }
    }

    li:before {
      content: '';
      display: block;
      width: 58px;
      min-width: 58px;
      height: 58px;
      margin-right: 16px;
      background: url('data:image/svg+xml;base64,') no-repeat;
    }
  }

  &__wrapper-pc {
    display: none;

    @media screen and (min-width: 1200px) {
      display: block;
    }
  }
}